.password {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #5d91f3;
}

.password__bg {
  position: absolute;
  left: calc(50% - 325px);
  top: calc(50% - 65px);
  width: 650px;
  height: 130px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  border: none;
  overflow: hidden;
  background: black;
}

.password__bg-after {
  position: absolute;
  right: 25px;
  top: 39px;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  background: white;
  color: white;
  transition: right 0.5s, top 0.5s, width 0.5s, height 0.5s, border-radius 0.5s;
}

.password__bg-after.active {
  right: 0;
  top: 0;
  width: 101%;
  height: 101%;
  border-radius: 19px;
}

@keyframes showPassword {
  0% {
    right: 25px;
    top: 39px;
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background: white;
  }
  100% {
    right: 0;
    top: 0;
    width: 101%;
    height: 101%;
    border-radius: 10px;
    background: white;
  }
}

.password__container {
  position: absolute;
  left: calc(50% - 325px);
  top: calc(50% - 65px);
  width: 650px;
  height: 130px;
  display: flex;
  align-items: center;
}

.password__input {
  font-family: 'Roboto Mono', monospace;
  width: 450px;
  height: 130px;
  line-height: 130px;
  font-size: 50px;
  border: none;
  background: none;
  color: #5d91f3;
}

.password__input:active, .password__input:hover, .password__input:focus {
  outline: 0;
  outline-offset: 0;
}

.password__icon {
  position: relative;
  width: 100px;
  height: 130px;
  stroke: white;
}

.password__icon-container-lock {
  border-radius: 20px 0 0 20px;
}

.password__icon-container-eye {
  border-radius: 0 20px 20px 0;
  cursor: pointer;
}

.password__icon-lock, .password__icon-eye {
  position: absolute;
  width: 40px;
  height: 40px;
  left: calc(50% - 20px);
  top: calc(50% - 20px);
}

.password__icon-lock {
  font-size: 40px;
  line-height: 40px;
  color: white;
}

.password__icon-lock.active {
  color: black;
}

.password__icon-eye-frame {
  display: none;
}

.password__icon-eye-frame:last-child {
  display: block;
}

.password__icon-eye-line {
  fill: none;
  stroke: #565cc0;
  stroke-miterlimit: 0;
  stroke-width: 4px;
  stroke-linecap: round;
}

.password__icon-eye-circle {
  fill: #565cc0;
  stroke: #565cc0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
